import { NgModule, Injectable } from '@angular/core';
import { BrowserModule, HammerModule, HAMMER_GESTURE_CONFIG, HammerGestureConfig } from '@angular/platform-browser';
import * as Hammer from 'hammerjs';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';

import { SharedModule } from './shared/shared.module';

import { AppRoutingModule } from './app-routing.module';
import { PlayModule } from './play/play.module';
import { SocialModule } from './social/social.module';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { HelpComponent } from './help/help.component';

@Injectable()
class CustomHammerConfig extends HammerGestureConfig {
    overrides = <any>{
      'swipe': { direction: Hammer.DIRECTION_ALL }
    };
}

@NgModule({
    declarations: [
        AppComponent,
        HomeComponent,
        HelpComponent,
    ],
    imports: [
        BrowserModule,
        HammerModule,
        BrowserAnimationsModule,
        FormsModule,
        SharedModule, // truthy pipe not found without this for some reason
        AppRoutingModule,
        PlayModule,
        SocialModule,
    ],
    providers: [
        {
            provide: HAMMER_GESTURE_CONFIG,
            useClass: CustomHammerConfig
        }
    ],
    bootstrap: [AppComponent]
})
export class AppModule { }
