import { Component, OnInit } from '@angular/core';
import { ServerService } from 'src/app/services/server/server.service';
import { ActivatedRoute, Router } from '@angular/router';
import { StorageService } from 'src/app/services/storage/storage.service';
import { environment } from 'src/environments/environment';

@Component({
    selector: 'app-connect',
    templateUrl: './connect.component.html',
    styleUrls: ['./connect.component.scss']
})
export class ConnectComponent implements OnInit {

    isoCode = '';
    role = 'player';

    constructor(private serverService: ServerService, private storageService: StorageService, private router: Router, private route: ActivatedRoute) {
        this.role = route.snapshot.data.role;
    }

    ngOnInit(): void { }

    async startSession() {

        if (this.isoCode?.length !== 6) return;

        if (this.role === 'mirror') {
            await this.serverService.connectSession(this.isoCode.toUpperCase(), 'mirror');
        }
        else {
            await this.serverService.connectSession(this.isoCode.toUpperCase(), 'player');
        }
    }

    async tryPasteCode() {
        // shortcut for dev
        if (environment.production) return;
        try {
            const code = await navigator.clipboard.readText();
            if (code.length === 6) this.isoCode = code;
        }
        catch {}
    }
}
