import { Component } from '@angular/core';
import { GameModeBase } from '../gamemode-base';
import { PlayerClient } from '../services/server/client';

@Component({
    selector: 'app-play',
    templateUrl: './play.component.html',
    styleUrls: ['./play.component.scss']
})
export class PlayComponent extends GameModeBase {

    name = 'PlayComponent';
    role = 'player';
    defaultRouteCommand = ['connect'];
    exitPending = false;

    determineRoute(): string[] {
        if (
            this.server.activeSession.state.phase === 'lobby' ||
            !(this.server.activeClient as PlayerClient).state.enrolled
        ) return ['lobby'];
        else if (this.server.activeSession.state.phase === 'game') {
            if (this.server.activeSession.state.dealer === this.server.activeClient.id) return ['deal'];
            else return ['game'];
        }
        return null;
    }

    captureSwipe() {}

    exit() {
        // deliberate exit, clear the stored state so we don't attempt to reconnect on reload
        this.storage.clear();
        this.server.withdraw(); // server will close us
    }
}