import { Component } from '@angular/core';
import { GameModeBase } from '../gamemode-base';
import { GameState } from '../common/interfaces';

@Component({
    selector: 'app-social',
    templateUrl: './social.component.html',
    styleUrls: ['./social.component.scss']
})
export class SocialComponent extends GameModeBase {

    name = 'SocialComponent';
    role = 'host'; // will be overriden by route data in base class
    defaultRouteCommand = ['setup'];

    determineRoute(state: GameState) {
        if (state.phase === 'lobby') return ['lobby'];
        else if (state.phase === 'game') return ['game'];
        return null;
    }

    exit() {
        super.exit();
        if (this.role === 'host') {
            this.server.endGame();
        }
        else {
            this.server.socket?.close();
        }
    }

}
