import { Injectable, EventEmitter } from '@angular/core';
import { first } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { StorageService } from '../storage/storage.service';
import { Session } from './session';
import { Client, PlayerClient } from './client';
import { Message, ClientDetails, PlayerClientState, PlayerData, GameState, GameDetails, ClientState, CommandType } from 'src/app/common/interfaces';

const WS_URI = `${environment.secureServer ? 'wss' : 'ws'}://${environment.serverUri}`;
const HTTP_URI = `${environment.secureServer ? 'https' : 'http'}://${environment.serverUri}`;

class SessionInitData {
    id: string;
    name: string;
    hostDetails: {
        id: string,
        token: string
    };
}

@Injectable({
    providedIn: 'root'
})
export class ServerService {

    socket: WebSocket;
    _hasReceivedClientState = false;
    _hasReceivedGameState = false;

    activeSession: Session;
    activeClient: Client;
    pendingState: { client: ClientState, game: GameState };

    connected = new EventEmitter<boolean>();
    session = new EventEmitter<Session>();
    client = new EventEmitter<Client>();

    constructor(private storage: StorageService) { }

    get isConnected() {
        return this._hasReceivedGameState && this._hasReceivedClientState;
    }

    async createSession(settings) {
        const response = await fetch(`${HTTP_URI}/session`, { method: 'POST', body: JSON.stringify(settings), headers: { 'Content-Type': 'application/json' } });
        const result = (await response.json()) as SessionInitData; // need to check status
        return result;
    }

    async getSession(sessionId: string, hostToken: string) {
        const response = await fetch(`${HTTP_URI}/session/${sessionId}?token=${hostToken}`, { method: 'GET' });
        const result = (await response.json()) as unknown; // need to check status
        return result;
    }

    async connectSession(sessionId: string, role: string, reconnect?: { id: string, token: string }) {

        // if there's already a session in place it needs to be torn down (otherwise we end up with multiple websocket connections)

        let url = `${WS_URI}/session/${sessionId}?role=${role}`;
        if (reconnect) {
            url = `${url}&id=${reconnect.id}&token=${reconnect.token}`;
        }

        this.socket = await this._connectWebSocket(url);
        this.socket.addEventListener('error', err => this._handleSocketError(err));
        this.socket.addEventListener('message', ev => this._handleSocketMessage(ev));
        this.socket.addEventListener('close', ev => this._handleSocketClose(ev));

        this.activeSession = new Session(sessionId, this.socket);
        this.activeClient = role === 'player' ? new PlayerClient() : new Client();

        // until this happens activeSession/activeClient are in an undefined state
        await this.connected.pipe(first()).toPromise();

        this.storage.lastSession = sessionId;
        this.storage.lastRole = role;
        if (reconnect) {
            this.storage.lastId = reconnect.id;
            this.storage.lastToken = reconnect.token;
        }
    }

    _connectWebSocket(url: string) {
        return new Promise<WebSocket>((resolve, reject) => {

            const socket = new WebSocket(url);

            function onOpen(ev) {
                socket.removeEventListener('open', onOpen);
                socket.removeEventListener('error', onError);
                resolve(socket);
            }

            function onError(err) { // ideally try and swallow the network error, don't want that in console log
                socket.removeEventListener('open', onOpen);
                socket.removeEventListener('error', onError);
                reject(err);
            }

            socket.addEventListener('open', onOpen);
            socket.addEventListener('error', onError);
        });
    }

    enrollPlayer(name: string, photo: string) {
        this.socket.send(JSON.stringify({ type: CommandType.PlayerEnroll, name, photo }));
    }

    withdraw() {
        this.socket.send(JSON.stringify({ type: CommandType.Withdraw }));
    }

    startGame() {
        this.socket.send(JSON.stringify({ type: CommandType.StartGame }));
    }

    endGame() {
        this.socket.send(JSON.stringify({ type: CommandType.EndGame }));
    }

    drawPromptCard() {
        this.socket.send(JSON.stringify({ type: CommandType.DrawDealerCard }));
    }

    revealPromptCard() {
        this.socket.send(JSON.stringify({ type: CommandType.RevealDealerCard }));
    }

    playCards(cards: number[]) {
        this.socket.send(JSON.stringify({ type: CommandType.PlayCards, cards }));
    }

    gatherCards() {
        this.socket.send(JSON.stringify({ type: CommandType.GatherCards }));
    }

    revealPlayerCards(cardSetId: string) {
        this.socket.send(JSON.stringify({ type: CommandType.RevealPlayerCards, cardSetId }));
    }

    selectWinner(cardSetId: string) {
        this.socket.send(JSON.stringify({ type: CommandType.SelectWinner, cardSetId }));
    }

    endTurn() {
        this.socket.send(JSON.stringify({ type: CommandType.EndTurn }));
    }

    _finishConnect() {
        // emit first, crucial details available (ids etc)
        this.connected.emit(true);
    }

    _handleSocketError(ev: Event) { // there's a chance a reconnect could fix this
        console.log('socket error');
        this.connected.emit(false);
        this._hasReceivedClientState = false;
        this._hasReceivedGameState = false
        this.activeSession = null;
        this.activeClient = null;
    }

    _handleSocketClose(ev: CloseEvent) { // there's a chance a reconnect could fix this
        console.log('socket closed');
        this.connected.emit(false);
        this._hasReceivedClientState = false;
        this._hasReceivedGameState = false
        this.activeSession = null;
        this.activeClient = null;
    }

    _handleSocketMessage(ev: MessageEvent) {
        console.log(`Message received: ${ev.data}`);

        const message = JSON.parse(ev.data) as Message;

        if (message.type === 'game-state-full') {
            this._handleGameStateFull(message.game);
        }
        else if (message.type === 'game-state-patch') {
            this._handleGameStatePatch(message.state);
        }
        else if (message.type === 'client-state-full') {
            this._handleClientStateFull(message.client);
        }
        else if (message.type === 'client-state-patch') {
            this._handleClientStatePatch(message.state);
        }
        else if (message.type === 'player-add') {
            this._handlePlayerAdd(message.player);
        }
        else if (message.type === 'player-update') {
            this._handlePlayerUpdate(message.id, message.state);
        }
        else if (message.type === 'player-remove') {
            this._handlePlayerRemove(message.id);
        }
    }

    _handlePlayerAdd(player: ClientDetails<PlayerClientState>) { // @TODO not quite the right input type. Server should send only what we need
        const playerData: PlayerData = {
            id: player.id,
            connected: player.state.connected,
            enrolled: player.state.enrolled,
            name: player.state.name,
            photo: player.state.photo,
            score: player.state.score,
        };
        this.activeSession.players = [...this.activeSession.players, playerData];
        this.activeSession.observablePlayers.emit(this.activeSession.players);
    }

    _handlePlayerRemove(id: string) {
        this.activeSession.players = this.activeSession.players.filter(p => p.id !== id);
        this.activeSession.observablePlayers.emit(this.activeSession.players);
    }

    _handlePlayerUpdate(id: string, state: Partial<PlayerClientState>) {
        // this is ugly but for now the received info Partial<PlayerClientState> is a superset of the PlayerData we're interested in
        const player = this.activeSession.players.find(p => p.id === id);
        if (!player) {
            console.warn('Received player update for a player not currently tracked. Ignoring');
            return;
        }
        if ('enrolled' in state) {
            player.enrolled = state.enrolled;
        }
        if ('connected' in state) {
            player.connected = state.connected;
        }
        if ('name' in state) {
            player.name = state.name;
        }
        if ('photo' in state) {
            player.photo = state.photo;
        }
        if ('score' in state) {
            player.score = state.score;
        }

        // forces updates so anything that applies a pipe to the player list will be re-run
        this.activeSession.players = [...this.activeSession.players];
    }

    _handleGameStatePatch(patch: Partial<GameState>) {
        const state = Object.assign(this.activeSession.state, patch);
        this.activeSession.state = Object.assign(this.activeSession.state, patch);
        this.activeSession.observableState.emit(this.activeSession.state);
    }

    _handleGameStateFull(details: GameDetails) {

        // happens once on first connect
        this.activeSession.name = details.name;
        this.activeSession.id = details.id;
        this._handleGameStatePatch(details.state);

        // if we've already received client state but weren't already connected we now are
        if (!this.isConnected) {
            this._hasReceivedGameState = true;
            if (this.isConnected) this._finishConnect();
        }
    }

    _handleClientStatePatch(patch: Partial<ClientState>) {
        if (this.activeClient.role === 'player') {
            this._handlePlayerClientStatePatch(patch as Partial<PlayerClientState>);
        }
        // none of the others have state yet
    }

    _handlePlayerClientStatePatch(patch: Partial<PlayerClientState>) {
        const client = this.activeClient as PlayerClient;
        client.state = Object.assign(client.state, patch);

        // store reconnect details
        // @TODO storage service should be in charge of listening for and actioning this. not server's job
        if (patch.enrolled === true) {
            this.storage.lastId = client.id;
            this.storage.lastToken = client.token;
        }

        this.activeClient.observableState.emit(this.activeClient.state);
    }

    _handleClientStateFull(details: ClientDetails<ClientState>)
    {
        this.activeClient.id = details.id;
        this.activeClient.role = details.role;
        this.activeClient.token = details.token;
        this._handleClientStatePatch(details.state);

        // store reconnect details if host (mirror doesn't store reconnect details at all and player stores later once enrolled)
        // @TODO storage service should be in charge of listening for and actioning this. not server's job
        if (this.activeClient.role === 'host') {
            this.storage.lastId = details.id;
            this.storage.lastToken = details.token;
        }

        if (this.activeClient.role === 'player') {
            this._handlePlayerClientStatePatch(details.state as Partial<PlayerClientState>);
        }

        // if we've already received game state but weren't already connected we now are
        if (!this.isConnected) {
            this._hasReceivedClientState = true;
            if (this.isConnected) this.connected.emit(true);
        }
    }
}
