import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PlayComponent } from './play.component';
import { ConnectComponent } from '../shared/shared.module';
import { PlayerLobbyComponent } from './lobby/player-lobby.component';
import { PlayerGameComponent } from './game/player-game.component';
import { PlayerDealComponent } from './deal/deal.component';

const routes: Routes = [
    {
        path: 'play',
        component: PlayComponent,
        children: [
            { path: 'connect', component: ConnectComponent, data: { role: 'player' } },
            { path: 'lobby', component: PlayerLobbyComponent },
            { path: 'game', component: PlayerGameComponent },
            { path: 'deal', component: PlayerDealComponent },
        ]
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PlayRoutingModule { }
