import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SocialComponent } from './social.component';
import { SetupComponent } from './setup/setup.component';
import { SocialLobbyComponent } from './lobby/social-lobby.component';
import { SocialGameComponent } from './game/social-game.component';
import { ConnectComponent } from '../shared/components/connect/connect.component';

const routes: Routes = [
    {
        path: 'social',
        component: SocialComponent,
        children: [
            { path: 'setup', component: SetupComponent },
            { path: 'lobby', component: SocialLobbyComponent, data: { role: 'host' } },
            { path: 'game', component: SocialGameComponent, data: { role: 'host' } },
        ],
        data: { role: 'host' }
    },
    {
        path: 'mirror',
        component: SocialComponent,
        children: [
            { path: 'setup', component: ConnectComponent, data: { role: 'mirror' } },
            { path: 'lobby', component: SocialLobbyComponent, data: { role: 'mirror' } },
            { path: 'game', component: SocialGameComponent, data: { role: 'mirror' } },
        ],
        data: { role: 'mirror' }
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class SocialRoutingModule { }
