import { EventEmitter } from '@angular/core';
import { GameState, Subphase, PlayerData } from 'src/app/common/interfaces';
// import { GameState, Phase, PlayerData } from './server.service';

class Session {

    name = '';
    players: PlayerData[] = [];
    state: GameState = {
        hostConnected: false,
        phase: 'unknown',
        numLurkers: 0,
        numMirrors: 0,
        responseDeckCount: 0,
        promptDeckCount: 0,
        dealer: '',
        subphase: Subphase.TurnBegin,
        revealedResponseCards: [],
        playersResponded: [],
        promptCard: null,
        winningCard: null
    };

    // player list emitter
    observablePlayers = new EventEmitter<PlayerData[]>();

    // state emitter
    observableState = new EventEmitter<GameState>();

    constructor(public id: string, public socket: WebSocket) {}
}

export { Session };
