import { Component, OnInit } from '@angular/core';
import { keyframes, style, trigger, transition, animate } from '@angular/animations';
import { ServerService } from 'src/app/services/server/server.service';
import { Session } from 'src/app/services/server/session';
import { Client } from 'src/app/services/server/client';
import { PlayerData, Subphase } from 'src/app/common/interfaces';

const flashFrames = keyframes([
    style({ color: "initial", offset: 0 }),
    style({ color: "hotpink", offset: 0.2 }),
    style({ color: "hotpink", offset: 0.8 }),
    style({ color: "initial", offset: 1 }),
]);

@Component({
    selector: 'app-social-game',
    templateUrl: './social-game.component.html',
    styleUrls: ['./social-game.component.scss'],
    animations: [
        trigger('flash', [
            transition('* => *', [ animate('1s', flashFrames) ]),
        ]),
    ]
})
export class SocialGameComponent implements OnInit {

    session: Session;
    client: Client;

    useFlipAnimationHack = navigator.userAgent.match('PlayStation 4');

    constructor(private server: ServerService) { }

    get dealer() {
        return this.session.players.find(p => this.session.state.dealer === p.id);
    }

    get otherPlayers() {
        return this.session.players.find(p => this.session.state.dealer !== p.id);
    }

    index = 0;

    ngOnInit() {
        this.session = this.server.activeSession;
        this.client = this.server.activeClient;
        this.index = 0;

        // @TODO only have this active while in the right phase. Reset index to zero and timer everytime a new card is added?
        setInterval(() => {
            if (this.session.state.promptCard) {
                this.index = ++this.index % this.session.state.promptCard.responses;
            }
            else {
                this.index = 0;
            }
        }, 1500);

        setInterval(() => {
            // keep server alive
            this.server.getSession(this.session.id, this.client.token);
        }, 60000);
    }

    getPlayerById(id: string) {
        return this.session.players.find(p => p.id === id);
    }

    get players() {
        return this.session.players.filter(p => p.enrolled);
    }

    get dealerFlipped() {
        return !!this.session.state.promptCard;
    }

    get showPlayerResponses() {
        return !this.showResponseCards;
    }

    get showResponseCards() {
        return [Subphase.DealerAnnouncingPlayerCards, Subphase.DealerJudging, Subphase.TurnResults].includes(this.session.state.subphase);
    }

}
