import { Component, OnInit, OnDestroy } from '@angular/core';
import { trigger, transition, style, animate, keyframes } from '@angular/animations';
import { Subscription } from 'rxjs';
import { ServerService } from 'src/app/services/server/server.service';
import { PlayerClient } from 'src/app/services/server/client';
import { Session } from 'src/app/services/server/session';
import { GameState, Subphase, CardSet } from 'src/app/common/interfaces';

const flashFrames = keyframes([
    style({ color: "initial", offset: 0 }),
    style({ color: "hotpink", offset: 0.2 }),
    style({ color: "hotpink", offset: 0.8 }),
    style({ color: "initial", offset: 1 }),
]);

@Component({
    selector: 'app-deal',
    templateUrl: './deal.component.html',
    styleUrls: ['./deal.component.scss'],
    animations: [
        trigger('noop', [
            transition(':enter', animate('0s'))
        ]),
        trigger('flash', [
            transition('* => *', [ animate('1s', flashFrames) ]),
        ]),
        trigger('modal', [
            transition(':enter', [
                style({ opacity: 0 }),
                animate('200ms', style({ opacity: 1 }))
            ]),
            transition(':leave', [
                style({ opacity: 1 }),
                animate('200ms', style({ opacity: 0 }))
            ])
        ]),
        trigger('fly-in', [
            transition(':enter', [
                style({ transform: 'translateY(-100%)' }),
                animate('100ms', style({ transform: 'translateY(0%)' }))
            ]),
        ]),
        trigger('fly-out', [
            transition(':leave', [
                style({ transform: 'translateY(0%)' }),
                animate('100ms', style({ transform: 'translateY(-100%)' }))
            ]),
        ])
    ],
})
export class PlayerDealComponent implements OnInit, OnDestroy {

    session: Session;
    player: PlayerClient;
    currentRevealCard = 0;
    stateSubscription: Subscription;
    endTurnCountdown = 3;

    // animation state
    promptRevealOn = false;
    responseRevealOn = false;
    cardSetRevealIndex = 0;
    cardSetEndReached = false;
    endTurnSeconds = 0;

    // tutorial state
    helpScreensDone = {
        'draw-prompt': true,
        'read-prompt': true,
        'gather': true,
        'announce': true,
        'browse': true,
        'award': true,
    };

    constructor(private server: ServerService) { }

    captureSwipe() {}

    ngOnInit() {
        this.session = this.server.activeSession;
        this.player = this.server.activeClient as PlayerClient;

        const hasDoneTutorials = localStorage.getItem('dealer-tutorial-complete');

        // enable tutorial screens
        if (!hasDoneTutorials) {
            for (const key in this.helpScreensDone) {
                this.helpScreensDone[key] = false;
            }
        }

        this.stateSubscription = this.session.observableState.subscribe((state: GameState) => {

            if (state.subphase === Subphase.TurnBegin) {

                // reset animation state
                this.promptRevealOn = false;
                this.responseRevealOn = false;
                this.endTurnSeconds = 0;

                // save tutorial progress
                if (!hasDoneTutorials) {
                    localStorage.setItem('dealer-tutorial-complete', 'true');
                }
            }

            if (state.subphase === Subphase.DealerAnnouncingPlayerCards) {
                this.cardSetRevealIndex = 0;
                this.cardSetEndReached = this.session.state.promptCard.responses === 1;
            }
        });
    }

    ngOnDestroy() {
        this.stateSubscription?.unsubscribe();
    }

    get statusText() {
        switch (this.session.state.subphase) {
            case Subphase.TurnBegin:
                return 'Draw a Prompt';
            case Subphase.DealerAnnouncingCard:
                return 'Read and Reveal';
            case Subphase.DealerAnnouncingPlayerCards:
                if (this.session.state.promptCard.responses === 1) {
                    return 'Read and Reveal';
                }
                else {
                    return `Read and Reveal (${this.session.state.promptCard.responses} cards/player)`;
                }
            case Subphase.PlayersPlaying:
                return 'Waiting on Players';
            case Subphase.AwaitingDealerGather:
                return 'Gather Responses';
            case Subphase.DealerJudging:
                return 'Pick a Winner!';
            case Subphase.TurnResults:
                return 'Results';
            default:
                return this.session.state.subphase as string;
        }
    }

    get subStatusText() {
        if (this.session.state.subphase === Subphase.DealerAnnouncingPlayerCards) {
            return `${this.currentRevealCard + 1} of ${this.player.state.responseOptions.length}`;
        }
        else return 'Dealer';

    }

    drawPromptCard() {
        this.server.drawPromptCard();
    }

    revealPromptCard() {
        this.promptRevealOn = true;
        this.server.revealPromptCard();
    }

    gatherCards() {
        this.server.gatherCards();
    }

    bumpSetIndex() {
        this.cardSetRevealIndex = ++this.cardSetRevealIndex % this.session.state.promptCard.responses;
        if (this.cardSetRevealIndex === this.session.state.promptCard.responses - 1) {
            this.cardSetEndReached = true;
        }
    }

    revealPlayerCard() {
        if (!this.cardSetEndReached) {
            return;
        }
        this.cardSetRevealIndex = 0;
        this.server.revealPlayerCards(this.player.state.responseOptions[this.currentRevealCard].id);
        this.currentRevealCard = ++this.currentRevealCard % this.player.state.responseOptions.length;
    }

    selectWinner(cardset: CardSet) {
        this.server.selectWinner(cardset.id);

        this.endTurnSeconds = this.endTurnCountdown;
        const interval = setInterval(() => {
            this.endTurnSeconds--;
            if (this.endTurnSeconds === 0) {
                clearInterval(interval);
            }
        }, 1000);
    }

    endTurn() {
        this.server.endTurn();
    }

    debugAnim(event, stage) {
        console.log(`${event.triggerName} ${stage} (${event.fromState} => ${event.toState})`);
    }

}