import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class StorageService {

    #lastSession: string = null;
    #lastRole: string = null;
    #lastToken: string = null;
    #lastId: string = null;

    constructor() {
        this.#lastSession = localStorage.getItem('lastSession');
        this.#lastRole = localStorage.getItem('lastRole');
        this.#lastToken = localStorage.getItem('lastToken');
        this.#lastId = localStorage.getItem('lastId');
    }

    set lastSession(value) {
        this.#lastSession = value;
        localStorage.setItem('lastSession', this.#lastSession);
    }

    get lastSession() {
        return this.#lastSession;
    }

    set lastRole(value) {
        this.#lastRole = value;
        localStorage.setItem('lastRole', this.#lastRole);
    }

    get lastRole() {
        return this.#lastRole;
    }

    set lastToken(value) {
        this.#lastToken = value;
        localStorage.setItem('lastToken', this.#lastToken || '');
    }

    get lastToken() {
        return this.#lastToken;
    }

    set lastId(value) {
        this.#lastId = value;
        localStorage.setItem('lastId', this.#lastId || '');
    }

    get lastId() {
        return this.#lastId;
    }

    clear() {
        localStorage.removeItem('lastId');
        localStorage.removeItem('lastToken');
        localStorage.removeItem('lastRole');
        localStorage.removeItem('lastSession');
    }
}
