import { Component, OnInit, OnDestroy } from '@angular/core';
import { trigger, transition, style, animate, state, keyframes } from '@angular/animations';
import { Subscription } from 'rxjs';
import { ServerService } from 'src/app/services/server/server.service';
import { Session } from 'src/app/services/server/session';
import { PlayerClient } from 'src/app/services/server/client';
import { GameState, Subphase, PlayerClientState, Card } from 'src/app/common/interfaces';

type Anim = 'left' | 'right' | 'play' | 'draw';

const flashFrames = keyframes([
    style({ color: "initial", offset: 0 }),
    style({ color: "hotpink", offset: 0.2 }),
    style({ color: "hotpink", offset: 0.8 }),
    style({ color: "initial", offset: 1 }),
]);

@Component({
    selector: 'app-player-game',
    templateUrl: './player-game.component.html',
    styleUrls: ['./player-game.component.scss'],
    animations: [
        trigger('noop', [
            transition(':enter', animate('0s'))
        ]),
        trigger('flash', [
            transition('* => *', [ animate('2s', flashFrames) ]),
        ]),
        trigger('flash-fast', [
            transition('* => *', [ animate('1s', flashFrames) ]),
        ]),
        trigger('modal', [
            transition(':enter', [
                style({ opacity: 0 }),
                animate('200ms', style({ opacity: 1 }))
            ]),
            transition(':leave', [
                style({ opacity: 1 }),
                animate('200ms', style({ opacity: 0 }))
            ])
        ]),
        trigger('fly-in', [
            transition(':enter', [
                style({ transform: 'translateY(-100%)' }),
                animate('100ms', style({ transform: 'translateY(0%)' }))
            ]),
        ]),
    ],
})

export class PlayerGameComponent implements OnInit, OnDestroy {

    session: Session;
    player: PlayerClient;
    stateSubscription: Subscription;

    // transient state, won't perisist through a reload
    currentCard = 0;
    playerHasPlayed = false; // = 'players-playing' AND not dealer AND not isPlayersTurn
    playerHasRedrawn = false;
    cardsPlayed: Card[] = [];

    // tutorial state
    browseCount = 0;
    helpScreensDone = {
        'browse-response': true,
        'select-response': true,
        'draw-new': true,
        'enjoy': true,
        'finish': true,
    };

    constructor(private server: ServerService) { }

    captureSwipe() {}

    ngOnInit() {

        this.session = this.server.activeSession;
        this.player = this.server.activeClient as PlayerClient;

        const hasDoneTutorials = localStorage.getItem('player-tutorial-complete');

        // enable tutorial screens
        if (!hasDoneTutorials) {
            for (const key in this.helpScreensDone) {
                this.helpScreensDone[key] = false;
            }
        }

        this.stateSubscription = this.session.observableState.subscribe((state: GameState) => {
            // this doesn't occur when transitioning from dealer, or first visit state has already been handled before the route change
            if (state.subphase === Subphase.TurnBegin) {

                // reset client-managed state
                this.playerHasPlayed = false;
                this.playerHasRedrawn = false;
                this.cardsPlayed = [];

                // save tutorial progress
                if (!hasDoneTutorials) {
                    localStorage.setItem('player-tutorial-complete', 'true');
                }
            }
        });
    }

    ngOnDestroy() {
        this.stateSubscription?.unsubscribe();
    }

    get statusText() {
        switch (this.session.state.subphase) {
            case Subphase.TurnBegin:
            case Subphase.AwaitingDealerGather:
                return 'Waiting on Dealer'
            case Subphase.DealerAnnouncingCard:
            case Subphase.DealerAnnouncingPlayerCards:
                return 'Listen to Dealer';
            case Subphase.PlayersPlaying:
                return this.player.state.isPlayersTurn ?
                    `Playing card ${this.cardsPlayed.length + 1} of ${this.session.state.promptCard.responses}` :
                    `Waiting for other Players`;
            case Subphase.DealerJudging:
                return 'Dealer Passing Judgement';
            case Subphase.TurnResults:
                return 'Results';
            default:
                return this.session.state.subphase as string;
        }
    }

    get subStatusText() {
        if (this.playerHasPlayed && !this.playerHasRedrawn) {
            return `Need ${this.session.state.promptCard.responses} card(s)`;
        }
        return `${this.currentCard + 1} of ${this.cards.length}`;
    }

    get cards() {
        return this.player.state?.cards?.filter(c => !this.cardsPlayed.includes(c)) ?? [];
    }

    nextCard() {
        this.browseCount++;
        this.currentCard = ++this.currentCard % this.cards.length;
    }

    previousCard() {
        this.browseCount++;
        this.currentCard--;
        if (this.currentCard < 0) {
            this.currentCard += this.cards.length;
        }
    }

    playCurrentCard() {
        const cards = this.cards;
        if (!this.player.state.isPlayersTurn) return;
        this.cardsPlayed.push(cards[this.currentCard]);
        if (this.cardsPlayed.length === this.session.state.promptCard.responses) {
            this.server.playCards(this.cardsPlayed.map(c => c.id));
            this.playerHasPlayed = true;
            this.currentCard = this.player.state.cards.length - this.session.state.promptCard.responses;
            this.cardsPlayed = [];
        }
        else if (this.currentCard === (cards.length - 1)) {
            this.currentCard = ++this.currentCard % cards.length;
        }
    }

    drawPlayerCards() {
        if (this.player.state.isPlayersTurn || !this.playerHasPlayed || this.playerHasRedrawn) return;
        this.playerHasRedrawn = true;
    }

}
