
import { EventEmitter } from '@angular/core';
import { Role, ClientState, PlayerClientState } from 'src/app/common/interfaces';

class Client {
    id: string;
    token: string;
    role: Role;
    state: ClientState = {};

    // state emitter
    observableState = new EventEmitter<ClientState>();

    constructor() { }
}

class PlayerClient extends Client {
    state: PlayerClientState = {
        name: '',
        photo: '',
        connected: false,
        enrolled: false,
        cards: [],
        isDealer: false,
        score: 0,
        isPlayersTurn: false,
        promptCard: null,
        responseOptions: []
    };

    // state emitter
    observableState = new EventEmitter<PlayerClientState>();
}

export { Client, PlayerClient };
